import {
  NavigationLink,
  NavLists,
  StyledNavigation,
  NavList,
} from "../../Navigation/styled";
import Header from "../../common/Header";
import Section from "../../common/Section";

const Author = () => {
  return (
    <>
      <StyledNavigation>
        <NavList>
          <NavLists>
            <NavigationLink to="/to-do-list-React">Zadania</NavigationLink>
          </NavLists>
          <NavLists bolded>
            <NavigationLink to="/o-autorze">O autorze</NavigationLink>
          </NavLists>
        </NavList>
      </StyledNavigation>
      <Header title="O autorze"></Header>
      <Section title="Joanna Panimasz" />
      <Section
        body={
          <>
            Mam na imi Joanna. Jestem programistk Front-end, pasjonatk
            technologii i brany IT. Zawsze stawiam sobie ambitne cele i d do
            ich realizacji z maksymaln starannoci. Moja pasja do
            programowania narodzia si, gdy projektowaam rnego rodzaju
            grafiki. Zafascynowao mnie, jak dziaa to, co projektuj, i ta
            ciekawo poprowadzia mnie w stron programowania. Front-end
            Development daje mi ogromn satysfakcj, poniewa pozwala tworzy
            funkcjonalne rozwizania, ktre s uyteczne dla innych. Ponadto,
            lubi widzie efekty pracy, co daje mi motywacj do dalszego
            dziaania i udoskonalenia. Kady projekt to dla mnie okazja do
            nauki, nieustannego rozwoju, co sprawia, e praca w tej brany jest
            nie tylko satysfakcjonujca, ale rwnie pena wyzwa i inspiracji.
          </>
        }
      ></Section>
    </>
  );
};

export default Author;
